#texts{
    position: absolute;
    width: calc(100% - 9px);
    border: none;
    height: calc(100% - 3vh - 4px);
    font-size: 2vh;
    font-weight: 500;
    outline: none;
    z-index: 1;
}
.options{
    position: absolute;
    top: calc(3vh -4px);
    right: 4px;
    z-index: 2;
    background:rgba(88, 88, 88, 0.719);
    
    width:20vh;
    
}
.options #head{
    padding-left: 3vh;
    background-color: rgb(231, 231, 231);
    font-weight: 500;
    font-size: 90%;
   
}
.options #option input,select{
    
    width: calc(100% );
    border: none;
    
    background: rgb(248, 246, 246);
    text-align: center;
    font-size: 2.5vh;
    font-weight: 500;
}

hr{
    color: black;
    margin: 1px;
    
}

