.TaskBar{
    bottom: 0;
    left: 0;
    width: 100vw;
    position: fixed;
    background: rgba(0, 0, 0, 0.158);
    height:calc(4vh);
    display: grid;
    align-content: center;
    padding: 0;
    margin: 0;
    position: absolute;
    grid-template: "s e" 4vh / 4vh 1fr;
    
}
.Start{
    grid-area: s;
    width:4vh;
    height:4vh;
    background: rgba(255, 255, 255, 0);
    
   
}
.Start:hover{
    background: rgba(255, 255, 255, 0.329);
}
.elements{
    background: rgba(128, 128, 128, 0);
    grid-area: e;
    align-content: center;
    display: grid;
    grid-template-columns:repeat(15,auto);
    border: none;

}
.elements button{
    
    padding: 1px;
    overflow: hidden;
    width: 100%;
    text-align: center;
    background: rgba(114, 100, 74, 0);
    height:4vh;
    color: white;
    align-content: center;
    border: none;
    font-weight: 500;
}



